.Canvas {
  position: relative;
  height: 80vh;
  width: 100%;
  border: solid;
  border-width: 1px 1px 1px 0;
  overflow: hidden;
}

.Canvas-svg {
  width: 100%;
  height: 100%;
}

#smallGrid,
#grid {
  fill: none;
  stroke: #a8a8a8;
  stroke-width: 0.25;
  shape-rendering: crispEdges;
}

#grid {
  stroke-width: 1;
}

.Canvas-element-selected rect {
  stroke: var(--green);
  stroke-width: 6;
}

.Canvas-rect {
  fill: #545454;
  stroke: #333;
  stroke-width: 2;
  cursor: pointer;
}

.Canvas-text {
  fill: #fff;
  pointer-events: none;
}

.Canvas-text-pax {
  font-size: 14px;
  font-weight: 300;
}

.Canvas-zoom {
  position: absolute;
  bottom: 10px;
  right: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 175px;
}


